.swiper-user-recommend {
  width: 100%;
  height: 100%;

  .swiper-slide {
    height: 116px;
    display: flex;
    justify-content: center;
    align-items: flex-start; }

  .swiper-button-next, .swiper-button-prev {
    top: 95%;
    border: none;
    color: $primary;
    background: transparent;
    &::before {
      content: '';
      position: absolute;
      width: 45px;
      top: 20px;
      border-top: solid 5px;
      border-radius: 5px; }
    &::after {
      font-size: 38px;
      font-weight: 700; } }

  .swiper-button-next {
    &::before {
      right: 4px; } }

  .swiper-button-prev {
    &::before {
      left: 4px; } } }
