.datepicker-dropdown .datepicker-picker {
  box-shadow: none; }

.datepicker-picker {
  border: solid 1px $primary; }

.datepicker {
  color: $primary; }

.datepicker-cell.selected, .datepicker-cell.selected:hover {
  font-weight: 400;
  background: $primary; }

.datepicker-cell:not(.disabled):hover {
  color: $light;
  background: $primary; }
